import React from "react";
import { Key } from "../Key/Key";

import "./Letters.scss";

export const Letters = () => {
  const alphabet = "abcdefghijklmnopqrstuvwxyz".split("");
  console.log(alphabet);
  return (
    <div className="letters">
      {alphabet.map((letter) => {
        return <Key>{letter}</Key>;
      })}
    </div>
  );
};
